/**
 * Custom Properties for keeping the CSS 'DRY'!
 */
:root {
  --background-color: #fff;
  --color-blue: hsl(210, 88%, 56%);
  --color-grey: hsl(213, 30%, 29%);
  --color-grey-light: hsl(213, 30%, 59%);
  --color-grey-lighter: hsl(15, 31%, 79%);
  --color-orange: hsl(34, 26%, 89%);
  --color-orange-dark: hsl(34, 76%, 89%);
  --color-red-light: hsl(7, 63%, 67%);
}

.game {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.game__board {
  font-family: "Noto Sans", sans-serif;
  font-size: 26px;
  margin: 30px 0;
  border: 2px solid hsl(213, 30%, 29%);
  border: 2px solid var(--color-grey);
  border-collapse: collapse;
}

.game__row:nth-child(3n) {
  border-bottom: 2px solid hsl(213, 30%, 29%);
  border-bottom: 2px solid var(--color-grey);
}

.game__cell {
  border: 1px solid hsl(15, 31%, 79%);
  border: 1px solid var(--color-grey-lighter);
  padding: 12px 16px;
  color: hsl(210, 88%, 56%);
  color: var(--color-blue);
  cursor: pointer;
  height: 30px;
}

.game__cell.game__cell--filled {
  color: hsl(210, 88%, 56%);
}

.game__cell.game__cell--userfilled {
  color: hsl(213, 30%, 59%);
  color: var(--color-grey-light);
}

.game__cell.game__cell--selected {
  color: hsl(34, 26%, 89%);
  color: var(--color-orange);
  background-color: hsl(34, 26%, 89%);
  background-color: var(--color-orange);
}

.game__cell:hover {
  background-color: hsl(34, 26%, 89%);
  background-color: var(--color-orange);
}

.game__cell.game__cell--filled.game__cell--selected {
  color: hsl(213, 30%, 29%);
  color: var(--color-grey);
  background-color: hsl(34, 26%, 89%);
  background-color: var(--color-orange);
}

.game__cell.game__cell--userfilled.game__cell--selected {
  color: hsl(213, 30%, 59%);
  color: var(--color-grey-light);
  background-color: hsl(34, 26%, 89%);
  background-color: var(--color-orange);
}

.game__cell.game__cell--highlightselected {
  color: hsl(34, 76%, 89%);
  color: var(--color-orange-dark);
  background-color: hsl(34, 76%, 89%);
  background-color: var(--color-orange-dark);
}

.game__cell.game__cell--filled.game__cell--highlightselected {
  color: hsl(213, 30%, 29%);
  color: var(--color-grey);
  background-color: hsl(34, 76%, 89%);
  background-color: var(--color-orange-dark);
}

.game__cell.game__cell--userfilled.game__cell--highlightselected {
  color: hsl(213, 30%, 59%);
  color: var(--color-grey-light);
  background-color: hsl(34, 76%, 89%);
  background-color: var(--color-orange-dark);
}

.game__cell:nth-child(3n) {
  border-right: 2px solid hsl(213, 30%, 29%);
  border-right: 2px solid var(--color-grey);
}

.status__numbers {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 30px;
}

.status__number {
  text-align: center;
  font-family: "Noto Sans", sans-serif;
  font-size: 26px;
  padding: 12px 0;
  cursor: pointer;
  border: 0.25px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin: 2px;
  transition: 0.5s ease;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.status__number:hover {
  background-color: #eaeef4;
  color: var(--color-blue);
  -webkit-transform: translate(1.5px, -1.5px);
          transform: translate(1.5px, -1.5px);
}

.innercontainer {
  display: -webkit-flex;
  display: flex;
  color: var(--color-grey);
  -webkit-flex-direction: row;
          flex-direction: row;
}

